.tile-text {
  line-height: 48px;
  font-size: 19px;
  /*font-family: 'Nunito', sans-serif;*/
  font-weight: 600;
  display: block;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  user-select: none;
}

.tile-photo {
  width: 48px;
  min-width: 48px;
  height: 48px;
  min-height: 48px;
  border-radius: 50%;
  overflow: hidden;
  object-fit: cover;
  position: relative;
  z-index: 1;
}

.tile-saved-messages {
  height: 24px;
  width: 24px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: #ffffff;
}
